import { GameController } from './game-controller';
import { PlayerManager } from './player-manager';
import { Player } from './player';
import { EColor } from './color';
import { Config } from './config';
import { EKeyCode, EMouseClick } from './keyboard-controls';
import { EventHandler } from './event-handler';
window.onload = function () {
    Config.canvasWidth = IS_ELECTRON_BUILD ? document.body.clientWidth : document.body.clientWidth - 20;
    Config.canvasHeight = IS_ELECTRON_BUILD ? document.body.clientHeight : document.body.clientHeight - 50;
    var canvasId = 'canvas';
    var canvasElement = document.getElementById(canvasId);
    if (!canvasElement) {
        throw new Error('No canvas element found');
    }
    canvasElement.width = Config.canvasWidth;
    canvasElement.height = Config.canvasHeight;
    var drawingContext;
    if (canvasElement.getContext) {
        drawingContext = canvasElement.getContext('2d');
    }
    else {
        throw new Error('No canvas support');
    }
    drawIntroText();
    EventHandler.oneTimeEventListener(document, 'keydown', initGame);
    function drawIntroText() {
        var frameLineWidth = 5;
        drawingContext.lineWidth = frameLineWidth;
        drawingContext.strokeStyle = '#6E6E6E';
        drawingContext.strokeRect(frameLineWidth / 2, frameLineWidth / 2, Config.canvasWidth - frameLineWidth, Config.canvasHeight - frameLineWidth);
        drawingContext.textAlign = 'middle';
        var fontSize = Config.canvasHeight / 10;
        drawingContext.font = fontSize + "px Fredericka the Great, cursive";
        drawingContext.fillStyle = '#ffffff';
        var textString = 'Achtung, die Kurve !';
        var textWidth = drawingContext.measureText(textString).width;
        drawingContext.fillText(textString, (Config.canvasWidth / 2) - (textWidth / 2), (Config.canvasHeight / 2) + (fontSize / 2));
        drawingContext.textAlign = 'middle';
        drawingContext.font = "lighter 16px Helvetica, Arial, sans-serif";
        drawingContext.fillStyle = '#ffffff';
        textString = 'Press any key to continue';
        textWidth = drawingContext.measureText(textString).width;
        drawingContext.fillText(textString, (Config.canvasWidth / 2) - (textWidth / 2), Config.canvasHeight - 50);
    }
    function initGame() {
        var playerManager = new PlayerManager();
        var player1 = new Player(1, 'Player 1', EColor.Red, EKeyCode.One, EKeyCode.Q);
        playerManager.addPlayer(player1);
        var player2 = new Player(2, 'Player 2', EColor.Yellow, EKeyCode.Y, EKeyCode.X);
        playerManager.addPlayer(player2);
        var player3 = new Player(3, 'Player 3', EColor.Orange, EKeyCode.M, EKeyCode.Comma);
        playerManager.addPlayer(player3);
        var player4 = new Player(4, 'Player 4', EColor.Green, EKeyCode.Left, EKeyCode.Down);
        playerManager.addPlayer(player4);
        var player5 = new Player(5, 'Player 5', EColor.Pink, EKeyCode.P, EKeyCode.SZ);
        playerManager.addPlayer(player5);
        var player6 = new Player(6, 'Player 6', EColor.Blue, EMouseClick.Left, EMouseClick.Right);
        playerManager.addPlayer(player6);
        var game = new GameController(drawingContext, playerManager);
    }
};
